import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { environment } from "environments/environment";
import {
  IAddEditExternalOrganiztion,
  IExternalOrganiztion,
} from "app/dashboard/models/setup/financial-setup/suppliers-and-customers";
@Injectable({
  providedIn: "root",
})
export class SuppliersAndCustomersService {
  constructor(private http: HttpClient) {}

  //Return All External Orgnizations

  getExternalOrgnizations(page: number, EduCompId: number) {
    return this.http.get(
      `${environment.baseUrl}ExternalOrg/getAllExternalOrg//${EduCompId}/${page}`
    );
  }
  //Return All External Orgnizations dropdown

  getExternalOrgnizationsDropDown(EduCompId: number) {
    return this.http.get<{ ExternalOrgList: IExternalOrganiztion[] }>(
      `${environment.baseUrl}ExternalOrg/getAllExternalOrg/${EduCompId}`
    );
  }

  //Add Edit External Orgnization
  addEditExternalOrgnization(data: IAddEditExternalOrganiztion) {
    return this.http.post(
      `${environment.baseUrl}ExternalOrg/addEditExternalOrg`,
      data
    );
  }

  //Delete External Orgnization
  deleteExternalOrgnization(Id: number) {
    return this.http.delete(
      `${environment.baseUrl}/ExternalOrg/deleteExternalOrg/${Id}`
    );
  }
}
