import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { environment } from "environments/environment";
import { IDiscountType } from "app/dashboard/models/financial/discount-type";
@Injectable({
  providedIn: 'root'
})
export class DiscountTypeService {

  constructor(private http: HttpClient) {}
  // Return All Discount Types
  getDiscountTypes(page: number,EduCompId:number) {
    return this.http.get(
      `${environment.baseUrl}DiscountType/getAllDiscountTypes/${EduCompId}/${page}`
    );
  }
  getDiscountTypeDropdown(EduCompId:number) {
    return this.http.get(
      `${environment.baseUrl}DiscountType/getAllDiscountType/${EduCompId}`
    );
  }

  //Add New Discount Type
  addEditDiscountType(data: IDiscountType) {
    return this.http.post(
      `${environment.baseUrl}DiscountType/addEditDiscountType`,
      data
    );
  }



  //Delete Discount Type
  deleteDiscountType(Id: Number) {
    return this.http.delete(
      `${environment.baseUrl}DiscountType/deleteDiscountType/${Id}`

    );
  }}
