import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { IEmployeesVM } from "app/dashboard/models/control/control-employee";
import {
  IEmployeeGetResponseVM,
  IEmployeeVM,
} from "app/dashboard/models/employees/employee";
import { country } from "app/shared/models/general/general";
import { environment } from "environments/environment";
import { Observable } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class EmployeeService {
  constructor(private http: HttpClient) {}

  // Return All Countries
  getAllCountries() {
    return this.http.get<country[]>(
      `${environment.baseUrl}Employees/getAllCountries`
    );
  }

  //#region employees
  // Return All Employees
  getAllEmployees(page: number): Observable<IEmployeeGetResponseVM> {
    return this.http.get<IEmployeeGetResponseVM>(
      `${environment.baseUrl}Employees/GetAllEmployees/${page}`
    );
  }

  // Return All Employees > dropdown
  getEmployeeEmployeesDropDown(): Observable<IEmployeeVM[]> {
    return this.http.get<IEmployeeVM[]>(
      `${environment.baseUrl}Employees/GetAllEmployees`
    );
  }

  // Return Employee ById
  getEmployeeById(id: number): Observable<IEmployeeVM> {
    return this.http.get<IEmployeeVM>(
      `${environment.baseUrl}Employees/getEmployeeById/${id}`
    );
  }

  // add Employee
  addEmployee(employee: IEmployeeVM) {
    return this.http.post(
      `${environment.baseUrl}Employees/addEmployee`,
      employee
    );
  }

  // update Employee
  updateEmployee(id: number, employee: IEmployeeVM) {
    return this.http.put(
      `${environment.baseUrl}Employees/updateEmployee/${id}`,
      employee
    );
  }

  // delete Employee
  deleteEmployee(id: number) {
    return this.http.delete(
      `${environment.baseUrl}Employees/deleteEmployee/${id}`
    );
  }
  //#endregion
}
