import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { environment } from "environments/environment";
import { ISalaryItem } from "app/dashboard/models/setup/financial-setup/salary-item";
@Injectable({
  providedIn: "root",
})
export class SalaryItemService {
  constructor(private http: HttpClient) {}
  // Return All Salary Items
  getSalaryItems(page: number,EduCompId:number) {
    return this.http.get(
      `${environment.baseUrl}SalaryItems/getAllSalaryItem/${EduCompId}/${page}`
    );
  }
  getSalaryItemsDropdownMenu(EduCompId:number) {
    return this.http.get(
      `${environment.baseUrl}SalaryItems/getAllSalaryItem/${EduCompId}`
    );
  }

  //Add New || UpdateSalary Item
  addEditSalaryItem(data: ISalaryItem) {
    return this.http.post(
      `${environment.baseUrl}SalaryItems/addEditSalaryItem`,
      data
    );
  }



  //Delete Salary Item
  deleteSalaryItem(SalaryItemId: number) {
    return this.http.delete(
      `${environment.baseUrl}SalaryItems/deleteSalaryItem/${SalaryItemId}`
    );
  }
}
