import { environment } from "environments/environment";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  AddTeacherModel,
  ISubjectTeacherAddVM,
  ISubjectTeacherListVM,
} from "app/dashboard/models/users/teacher";

@Injectable({
  providedIn: "root",
})
export class TeacherService {
  constructor(private http: HttpClient) {}

  addUpdateTeacher(body: AddTeacherModel) {
    return this.http.post(`${environment.baseUrl}Parent/RegisterParent`, body);
  }

  getTeachers(page: number) {
    return this.http.get(`${environment.baseUrl}Parent/return-all/${page}`);
  }

  getTeacherById(parentId: number) {
    return this.http.get(
      `${environment.baseUrl}Parent/GetParentById/${parentId}`
    );
  }

  deleteTeacher(parentId: number) {
    return this.http.delete(
      `${environment.baseUrl}Parent/DeleteParentById/${parentId}`
    );
  }

  getAllTeacherCourses(page: number) {
    return this.http.get<ISubjectTeacherListVM>(
      `${environment.baseUrl}Parent/RegisterParent/${page}`
    );
  }

  addTeacherCourse(body: ISubjectTeacherAddVM) {
    return this.http.post(`${environment.baseUrl}Parent/RegisterParent`, body);
  }

  deleteTeacherCourse(id: number) {
    return this.http.delete(`${environment.baseUrl}Parent/RegisterParent/${id}`);
  }
}
